import Vue from 'vue';
import VueRouter from 'vue-router';

Vue.use(VueRouter);

import Home from '@/Home';

import music from './modules/music';
import article from './modules/article';

const routes = [
  {
    path: '/',
    name: 'home',
    component: Home,
    children: [
      ...music,
      ...article
    ]
  }

];

const router = new VueRouter({
  routes
});

export default router;
